<template>
 <div style="background-color: #EFF3F5;width: 100%;padding-top: 10px;padding-bottom: 80px;">
   <div class="container" style="background-color: #fff;">
     <div class="header">
       <h1>创建一个比特币交易广告</h1>
       <p>如果您经常进行BTC交易，您可以创建自己的交易广告。如果您只是偶尔交易，我们建议您直接搜索BitcoinWorld的交易广告。</p>
       <p>创建一则交易广告是免费的。</p>
       <p>在设置您的交易广告前，请阅读在线销售交易广告指南和现金交易指南。</p>
       <p>若您想直接编辑已创建的广告，请查看我的广告。</p>
       <p style="font-size:18px;color: #333;margin-top: 37px;">基本选项</p>
     </div>
     <p class="line"></p>
     <!--成功-->
     <div class="list-success" v-if="false">
       <img src="../../images/release/success.png" alt="">
       <p>发布成功</p>
     </div>
     <!--选项-->
     <div class="list-option">
       <div class="item">
         <h2><span>*</span>我想要</h2>
         <div>
           <el-radio class="radio" v-model="radio" label="2">在线出售</el-radio>
           <el-radio class="radio" v-model="radio" label="1">在线购买</el-radio>
           <el-radio class="radio" v-model="radio" label="4">本地出售</el-radio>
           <el-radio class="radio" v-model="radio" label="3">本地购买</el-radio>
           <p class="remark">(您想要创建什么样的交易广告？如果您希望出售比特币，请确保您在BitcoinWorld的钱包中有比特币)</p>
         </div>
       </div>
       <div class="item">
         <h2><span>*</span>国家</h2>
         <div>
           <div>
             <el-select v-model="country_id" placeholder="中国" style="width: 460px;height: 30px;">
               <el-option
                 v-for="item in options1"
                 :key="item.value"
                 :label="item.label"
                 :value="item.value">
               </el-option>
             </el-select>
           </div>
           <p class="remark">(请指定国家)</p>
         </div>
       </div>
       <div class="item">
         <h2><span>*</span>货币</h2>
         <div>
           <el-select v-model="money_type" placeholder="CNY" style="width: 460px;height: 30px;">
             <el-option
               v-for="item in options1"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
         </div>
       </div>
       <div class="item">
         <h2><span>*</span>溢价</h2>
         <div>
           <div style="display: flex;align-items: flex-end">
             <p style="border-bottom:1px solid #314E8C;width: 150px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
               <input type="number"
                      v-model="premium"
                      style="width:100px;text-align: center;font-size:24px;color: #314E8C;border: none;" maxlength="6">
               <span>%</span>
             </p>
             <div>
               <p style="width: 30px;height: 18px;line-height: 18px;text-align: center;background-color:#314E8C "><i class="el-icon-caret-top" style="color: #fff" @click="add"></i></p>
               <p style="width: 30px;height: 18px;line-height: 18px;text-align: center;background-color:#314E8C;margin-top: 3px; "><i class="el-icon-caret-bottom" style="color: #fff" @click="reduce"></i></p>
             </div>
           </div>
           <p class="remark">如果您想要比特币市场价格的溢价，您可以以高于市场价买入或低于市场价卖出的方式来并吸引更多的交易者。）</p>
         </div>
       </div>
       <div class="item">
         <h2><span style="margin-right: 18px"></span>交易价格</h2>
         <div>
           <p style="color: #E0180F;font-size:22px;">27862.00<span style="margin-left: 4px;font-size:12px;color: #314E8C">CNY/BTC</span></p>
           <p class="remark">(市场价格*溢价)</p>
         </div>
       </div>
       <div class="item" v-if="radio == 3 || radio == 4">
         <h2><span>*</span>见面地点</h2>
         <div>
           <p style="border-bottom:1px solid #314E8C;width: 460px;text-align: left;font-size:24px;color:#314E8C;display: flex">
             <input type="text" style="width:460px;text-align: left;font-size:24px;color: #314E8C;border: none;" maxlength="18">
           </p>
           <p class="remark">(您愿意在哪儿进行现金交易，例如本地有网络的咖啡馆或餐厅)</p>
         </div>
       </div>
       <div class="item" v-if="radio == 2">
         <h2><span>*</span>支付期限</h2>
         <div>
           <p style="border-bottom:1px solid #314E8C;width: 150px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
             <input type="number"
                    style="width:100px;text-align: center;font-size:24px;color: #314E8C;border: none;"
                    maxlength="6"
                    @change="chackMinutes"
                    v-model="minutes">
             <span>分钟</span>
           </p>
           <p class="remark">(您承诺在多少分钟之内进行付款, 请输入整数)</p>
         </div>
       </div>
       <div class="item">
         <h2><span>*</span>支付方式</h2>
         <div>
           <el-select v-model="payment_channel_ids" placeholder="请选择付款方式" style="width: 460px;height: 30px;">
             <el-option
               v-for="item in options1"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
         </div>
       </div>
       <div class="item">
         <h2><span style="margin-right: 18px"></span>产品描述</h2>
         <div>
           <el-input
             style="width: 460px;height: 140px;"
             type="textarea"
             :rows="6"
             placeholder="请输入内容"
             v-model="description">
           </el-input>
           <p class="remark">(可选项，付款方式备注信息)</p>
         </div>
       </div>
       <!--<div class="item">-->
         <!--<h2><span style="margin-right: 18px"></span>自动回复</h2>-->
         <!--<div>-->
           <!--<el-switch-->
             <!--v-model="value2"-->
             <!--on-text=""-->
             <!--off-text="">-->
           <!--</el-switch>-->
           <!--<p class="remark">(启用后，用户通过此广告向您发起交易时，系统自动将您选择的自动回复用语发送给对方)</p>-->
         <!--</div>-->
       <!--</div>-->
       <!--<div class="item">-->
         <!--<h2><span style="margin-right: 18px"></span>启用安全选项</h2>-->
         <!--<div>-->
           <!--<el-switch-->
             <!--v-model="value2"-->
             <!--on-text=""-->
             <!--off-text="">-->
           <!--</el-switch>-->
           <!--<p class="remark">(启用后，仅限于已验证身份和通过短信验证的用户与本广告交易)</p>-->
         <!--</div>-->
       <!--</div>-->
       <!--<div class="item">-->
         <!--<h2><span style="margin-right: 18px"></span>仅限受信任的交易者</h2>-->
         <!--<div>-->
           <!--<el-switch-->
             <!--v-model="value2"-->
             <!--on-text=""-->
             <!--off-text="">-->
           <!--</el-switch>-->
           <!--<p class="remark">(启用后，仅限于自己信任的用户与本广告交易)</p>-->
         <!--</div>-->
       <!--</div>-->
       <div class="item">
         <h2><span style="margin-right: 18px"></span>最小交易额</h2>
         <div>
           <p style="border-bottom:1px solid #314E8C;width: 140px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
             <input type="number" style="width:100px;text-align: center;font-size:24px;color: #314E8C;border: none;" maxlength="6">
             CNY
           </p>
           <p class="remark">(可选。每一笔交易的最低限额)</p>
         </div>
       </div>
       <div class="item">
         <h2><span style="margin-right: 18px"></span>最大交易额</h2>
         <div>
           <p style="border-bottom:1px solid #314E8C;width: 140px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
             <input type="number" style="width:100px;text-align: center;font-size:24px;color: #314E8C;border: none;" maxlength="6">
             CNY
           </p>
           <p class="remark">(可选。每一笔交易的最大限额。对于在线出售，您在 Eagle wallet 钱包里的比特币总量也会限制您的最大交易量)</p>
         </div>
       </div>
       <div class="flex-center" style="width: 100%;">
         <div class="btn flex-center" @click="createProducts()">
           <p>发布广告</p>
         </div>
       </div>
     </div>
   </div>
 </div>
</template>

<script>
import { mapState } from 'vuex'
import {httpGet,httpPost} from '../../utils/index'

export default {
  data () {
    return {
      radio: '2',
      options1: [{
        value: '1',
        label: '黄金糕'
      }, {
        value: '2',
        label: '双皮奶'
      }, {
        value: '3',
        label: '蚵仔煎'
      }, {
        value: '4',
        label: '龙须面'
      }, {
        value: '5',
        label: '北京烤鸭'
      }],
      payment_channel_ids: '',
      value2: true,
      input: '',
      description: '',
      premium:1,
      minutes:2,
      money_type:'',
      country_id:'',
      min_trade_amount:'',
      max_trade_amount:'',
      address:''
    }
  },
  computed: {
    ...mapState([''])
  },
  methods: {
    createProducts () {
      const that = this;
      httpPost('/products/create',{
        type: that.radio,
        country_id: that.country_id,
        symbol_type: '',
        money_type: '',
        price: '',
        premium: '',
        payment_channel_ids: '',
        minutes: '',
        description: '',
        min_trade_amount:'',
        max_trade_amount:'',
        address:'',
      },function (res) {
        if(res.data.error_code === 0){
          console.log('发布广告成功')
        }
      })
    },
    add(){
      this.premium++;
    },
    reduce(){
      this.premium--;
      if(this.premium < 0){
        this.premium = 0;
      }
    },
    chackMinutes(){
      if(this.minutes < 0){
        this.minutes = 0;
      }
    }
  }
}
</script>

<style scoped>
  .header{
    padding: 0 90px;
  }
  .header h1{
    font-family: PingFangSC-Regular;
    font-size: 24px;
    color: #314E8C;
    letter-spacing: -0.41px;
    padding-top: 64px;
    margin-bottom: 20px;
  }
  .header p{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #666666;
    letter-spacing: -0.24px;
  }
  .line{
    width: 1100px;
    height: 2px;
    background-color: #d8d8d8;
    margin: 0 auto;
    margin-top: 16px;
  }
  .item{
    padding: 0 70px;
    display: flex;
    margin-top: 50px;
    /*border: 1px solid #000;*/
  }
  .item h2{
    font-family: PingFangSC-Regular;
    font-size: 18px;
    color: #333333;
    letter-spacing: -0.31px;
    width: 200px;
  }
  .item h2 span{
    color: #FF0404;
    margin-right: 10px;
  }
  .item .remark{
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #999999;
    letter-spacing: -0.2px;
    margin-top: 10px;
  }
  .btn{
    width: 400px;
    height: 60px;
    margin: 0 auto;
    background-color: #314E8C;
    margin: 136px 0 84px 0;
  }
  .btn p{
    font-size:24px;
    color: #fff;
  }
  .list-success{
    width: 100%;
    height: 780px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
  .list-success img{
    width: 116px;
    height: 128px;
    margin-top: 120px;
  }
  .list-success p{
    ont-family: PingFangSC-Regular;
    font-size: 24px;
    color: #333333;
    letter-spacing: -0.41px;
    margin-top: 45px;
  }
</style>



