<template>
  <div class="content">
    <div class="box1">
      <p>新的密码</p>
      <el-input style="width: 260px;height: 50px;margin-left: 30px;"></el-input>
    </div>
    <div class="box1">
      <p>确认密码</p>
      <el-input style="width: 260px;height: 50px;margin-left: 30px;"></el-input>
    </div>
    <div class="box2" @click="next">
      <p>修改</p>
    </div>
    <div class="box3">为了保护您的账户安全，重置密码成功后的一天内无法进行交易、提现等资产相关操作，到期自动解除限制。</div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    data () {
      return {

      }
    },
    computed: {
      ...mapState({
        // isCollapse: state => state.isCollapse
      })
    },
    methods: {
      next(){
        this.$router.push('/')
      }
    }
  }
</script>

<style scoped>
  .box1{
    margin-left: 154px;
    margin-top: 56px;
    display: flex;
    align-items: center;
  }
  .box1 p{
    font-family: PingFangSC-Regular;
    font-size: 18px;
    color: #333333;
    letter-spacing: -0.31px;
  }
  .box2{
    margin-left: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
    background: #314E8C;
    border-radius: 4px;
    width: 260px;
    height: 48px;
  }
  .box2 p{
    font-family: PingFangSC-Regular;
    font-size: 20px;
    color: #FFFFFF;
    letter-spacing: -0.34px;
  }
  .box3{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #2A2A2A;
    letter-spacing: -0.24px;
    line-height: 20px;
    margin-left: 154px;
    margin-top: 40px;
  }
</style>



