<template>
  <div style="background-color: #EFF3F5;width: 100%;border: 1px solid #fff">
   <div class="container signin">
     <div class="s_title">注册新账号</div>
     <div class="s_line"></div>
     <div class="s_content">
       <div class="s_c_item">
         <span class="input_title">用户名：</span>
         <div class="input_item">
           <input @input="userbulr" v-model="username" :class="[{error:username_showError},'item']" type="text" placeholder="4-16位的非特殊字符，首字符必须为字母">
           <p :class="[{tip_error:username_showError},'input_item_tip']">4-16位的非特殊字符，首字符必须为字母</p>
         </div>
       </div>
       <div class="s_c_item">
         <span class="input_title">Email：</span>
         <div class="input_item">
           <input @input="emailbulr" v-model="useremail" :class="[{error:useremail_showError},'item']" type="text" placeholder="">
           <p v-if="useremail_showError" style="color: red" class="input_item_tip">邮箱格式不对</p>
         </div>
       </div>
       <div class="s_c_item">
         <span class="input_title">邮箱验证码：</span>
         <div class="input_item email_verification">
           <input class="item" type="text" placeholder="请输入邮箱验证码">
           <p class="input_item_tip">验证码15分钟内有效</p>
         </div>
         <div  @click = "getEmailVerif" :class="['email_btn' ,{email_btn_hide:email_btn_hide}]">获取</div>
         <div :class="['email_show', {email_show_time:email_show_time}]">
           <i>{{emailCount}}</i>s后获取
         </div>
       </div>
       <div class="s_c_item">
         <span class="input_title">密码：</span>
         <div class="input_item">
           <input @input="validateCode" maxlength="30" v-model="usercode" :class="['item' ,{error:usercode_error}]" type="text" placeholder="4-30位的非特殊字符">
           <p v-if="usercode_error" class="tip_error input_item_tip">{{usercode_error_message}}</p>
           <div v-if="levelFlag" class="level">
             <span :class="[{low:lowFlag}, 'level_item']">低</span>
             <span :class="[{middle:middleFlag}, 'level_item']">中</span>
             <span :class="[{high:highFlag}, 'level_item']">高</span>
           </div>
         </div>
       </div>
       <div class="s_c_item">
         <span class="input_title">确认密码：</span>
         <div class="input_item">
           <input @input="validateCode2" maxlength="30" v-model="usercode2_message" :class="['item' ,{error:usercode2_error}]" type="text" placeholder="请再次输入密码">
           <p v-if="usercode2_error" :class="[{tip_error:usercode2_error},'input_item_tip']">两次密码输入不一致</p>
         </div>
       </div>
       <div class="s_c_item">
         <span class="input_title">验证码：</span>
         <div class="input_item random_input">
           <input class="item error" type="text" placeholder="">
           <p class="tip_error input_item_tip"></p>
         </div>
         <div class="random_verification">
           <img class="img_random_verification" :src="random_verification" alt="图片验证码">
         </div>
         <div class="random_btn" @click="change_random_verification">看不清</div>
       </div>
       <div class="xieyi">
         <div v-if="toggleXieyi" class="xieyi_unread" @click="xieyi"></div>
         <div v-if="!toggleXieyi" class="xieyi_read" @click="xieyi"></div>
         我已阅读并同意
         <a href="" style="color: #314E8C">《eagle wallet网络服务条款》</a>

       </div>
       <div class="s_btn" @click="goSignin">注册</div>
       <div class="tologin">
         <span>已经有一个账户？</span>
         <a href="#login" style="color: #314E8C">登录</a>
       </div>
       <div class="resetCode">
         <span>忘记密码？{{hasLogin}}</span>
         <a href="" style="color: #314E8C">重置您的登录密码</a>
       </div>
     </div>
   </div>
  </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'
import { httpGet,httpPost,getStore } from '../../utils/index'
export default {
  data () {
    return {
      toggleXieyi:false,
      username:'',
      useremail:'',
      usercode:'',
      random_verification:'',
      random_verification_token:'',
      username_showError:false,
      useremail_showError:false,
      canSigin_username:false,
      canSigin_email:false,
      emailCount:900,
      email_show_time:false,
      email_btn_hide:false,
      canGet_email:true,
      levelFlag:true,
      usercode_error:false,
      usercode_error_message:'4-30位非特殊字符串',
      lowFlag:false,
      middleFlag:false,
      highFlag:false,
      usercode2_error:false,
      usercode2_message:''
    }
  },
  computed: {
    ...mapState(['hasLogin'])

  },
  mounted(){
    this.getData();
  },
  methods: {
    xieyi:function () {
      this.toggleXieyi = !this.toggleXieyi;
    },
    goSignin:function () {

      if (this.toggleXieyi){
        alert("请先同意协议！")
        return;
      };
      if(this.canSigin_username&this.canSigin_email){
        console.log('注册成功!');
      }else {
        console.log(this.canSigin_username,this.canSigin_email);
      }
    },
    getData:function () {
      var that = this;
      httpGet('/captcha',{},function (res) {
        if(res.data.error_code == 0){
          that.random_verification = res.data.image_data;
          that.random_verification_token = res.data
          console.log(that.random_verification,res.data.image_token);
        }else{
          console.log(res);
        }

      })
    },
    change_random_verification:function () {
      this.getData();
    },
    userbulr:function () {
      var regusername = /^[a-zA-Z]\w{3,15}$/;
      if(regusername.test(this.username)){
        console.log(this.username);
        this.username_showError = false;
        this.canSigin_username = true;
      }else{
        console.log('用户名非法');
        this.username_showError = true;
        this.canSigin_username= false;
        return;
      };
    },
    emailbulr:function () {
      var reguseremail =  /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
      if(reguseremail.test(this.useremail)){
        console.log(this.useremail);
        this.useremail_showError = false;
        this.canSigin_email = true;
      }else{
        console.log('邮箱非法');
        this.useremail_showError = true;
        this.canSigin_email= false;
        return;
      }
    },
    getEmailVerif:function () {
      var that = this;
      if (!this.useremail||!this.canSigin_email||!this.canGet_email){
        console.log("邮箱错误");
        return;
      }else{
        httpGet('/users/send_email',{email:this.useremail},function (res) {
          if(res.data.error_code == 0){
            var data = res.data;
            console.log(data,data.now_at);

            function GetRTime(){
              if(that.emailCount>0){
                that.canGet_email = false;
                that.email_show_time = true;
                that.email_btn_hide = true;
                that.emailCount -= 1;
              }else{
                that.email_show_time = false;
                that.email_btn_hide = false;
                clearInterval(timer1);
                that.canGet_email = true;
                that.emailCount = 900;
              }
            }
           var timer1 =  setInterval(GetRTime,1000);
          }

        })
        console.log("已发验证码");
      }
    },
    validateCode:function () {
      var regLetter = /[a-zA-Z]\w+/;
      var regNum = /\d+/;
      var regCommon = /[`~!@#%$^&*()=|_-{} ':;',\\[\。，、？]/;
      var complex = 0;
      if(!this.usercode){
        this.usercode_error = true;
        this.usercode_error_message = '密码不能为空';
        this.levelFlag = false;
        return;
      }else if(this.usercode.length<4||this.usercode.length>30){
        this.usercode_error = true;
        this.usercode_error_message = '长度在4-30位之间';
        return;
      }else{
        this.usercode_error = false;
        if (regLetter.test(this.usercode)) {
          ++complex;
        }
        if (regNum.test(this.usercode)) {
          ++complex;
        }
        if(regCommon.test(this.usercode)&this.usercode.length>10){
          ++complex;
        }
        if (complex == 1) {
          this.levelFlag = true;
          this.lowFlag = true;
          this.middleFlag = false;
          this.highFlag = false;
          console.log(complex,"低");
        } else if(complex == 2) {
          this.levelFlag = true;
          this.lowFlag = false;
          this.highFlag = false;
          this.middleFlag = true;
          console.log(complex,"中");
        }else if(complex == 3){
          this.levelFlag = true;
          this.lowFlag = false;
          this.middleFlag = false;
          this.highFlag = true;
          console.log(complex,"高");
        }else{
          this.usercode_error = true;
          this.usercode_error_message = '长度在4-30位之间';
          this.levelFlag = false;
          return;
          console.log(complex,"4-16非特殊字符");
        }
      }
    },
    validateCode2:function () {
      if (!this.usercode2_message || this.usercode2_message !== this.usercode){
        this.usercode2_error = true;
        return;
      }else{
        this.usercode2_error = false;
        console.log(this.usercode,this.usercode2_message);
        return;
      }
    }
  }
}
</script>

<style scoped>
  @import "../../common.css";
  i{font-style: normal}
  .signin{
    background-color: #fff;
    padding: 50px;
    margin-top: 20px;
  }
  .signin .s_title{
    margin-left: 150px;
    font-family: PingFangSC-Regular;
    font-size: 24px;
    color: #333333;
  }
  .signin .s_line{
    height: 2px;
    width: 100%;
    margin: 20px auto;
    background-color: #D8D8D8;
  }
  .s_content{
    /*background-color: rosybrown;*/
    padding: 50px 0 50px 150px;
  }
  .s_c_item{
    /*width: 610px;*/
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;
  }
  .s_c_item .input_title{
    width: 120px;
    min-width: 120px;
    height: 50px;
    line-height: 50px;
    padding-left: 10px;
    background: url("../../images/login/required.png") no-repeat 0 center;
    /*border:1px solid #000;*/
  }
  .s_c_item .email_btn{
    width: 105px;
    height: 50px;
    margin: 0 30px 0 20px;
    background: #314E8C;
    border-radius: 2px;
    text-align: center;
    line-height: 50px;
    ont-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
    cursor: pointer;
  }
  .s_c_item .email_btn_hide{
    background: #DDD;
  }
  .s_c_item .email_show{
    width: 110px;
    height: 50px;
    background: #DDDDDD;
    border-radius: 2px;
    text-align: center;
    line-height: 50px;
    ont-family: PingFangSC-Regular;
    font-size: 14px;
    color: #FFFFFF;
  }
  .s_c_item .email_show_time{
    background: #314E8C;
  }
  .s_c_item .random_verification{
    width: 92px;
    height: 40px;
    margin: 10px 10px 0 20px;
  }
  .random_verification .img_random_verification{
    width: 100%;
    height: 100%;
  }
  .s_c_item .random_btn{
    width: 50px;
    height: 50px;
    line-height: 50px;
  }
  .input_item{
  }
  .input_item input.item{
    width: 460px;
    height: 50px;
    border: 1px solid #ddd;
    line-height: 50px;
    padding-left: 20px;
    outline: none;
  }
  .input_item input.error{
    border: 1px solid red;
  }
  .email_verification input.item{
    width: 334px;
  }
  .random_input input.item{
    width: 105px;
  }
  .input_item .input_item_tip{
    width:460px;
    height: 20px;
    line-height: 20px;
  }
  .email_verification .input_item_tip{
    width: 334px;
  }
  .random_input .input_item_tip{
    width: 105px;
  }
  .input_item_tip{
    font-size: 12px;
    color: #999;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .tip_error{
    color: red;
  }
  .s_content .xieyi{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 30px auto;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #666666;
    /*background-color: red;*/
  }
  .s_content .xieyi .xieyi_unread{
    width: 14px;
    height: 14px;
    margin-right: 5px;
    background: url("../../images/login/unread.png") no-repeat;
    background-size: 14px 14px;
  }
  .s_content .xieyi .xieyi_read{
    width: 14px;
    height: 14px;
    margin-right: 5px;
    background: url("../../images/login/read.png") no-repeat;
    background-size: 14px 14px;
  }
  .s_content .s_btn{
    width: 260px;
    height:50px;
    margin: 30px auto;
    text-align: center;
    line-height: 50px;
    color: #fff;
    font-size: 20px;
    background: #314E8C;
    border-radius: 4px;
    cursor: pointer;
  }
  .s_content .tologin{
    width: 200px;
    margin: 0 auto 15px;
    font-family: PingFangSC-Regular;
    font-size: 18px;
    letter-spacing: -0.31px;
  }
  .s_content .resetCode{
    width: 260px;
    margin: 0 auto 15px;
    font-family: PingFangSC-Regular;
    font-size: 18px;
    letter-spacing: -0.31px;
  }
  /*密码强度*/
  .level{
    display: flex;
  }
  .level .level_item{
    display:none;
    width: 50px;
    height: 25px;
    text-align: center;
    line-height: 25px;
  }
  .level .low{
    background-color: red;
    display: block;
  }
  .level .middle{
    background-color: rosybrown;
    display: block;
  }
  .level .high{
    background-color: green;
    display: block;
  }
</style>



