<template>
  <div class="header">
    <div class="container flex-between">
      <div class="header-left">
        <span v-for="(item,index) in stationList">{{item.name}}：{{item.price}}</span>
      </div>
      <div style="display: flex;">
        <div class="header-right" v-if="!hasLogin">
          <router-link to="/login" tag="a">登录</router-link>
          <span style="display:inline-block;border-left: 1px solid #d8d8d8;width: 1px;height: 12px;margin: 0 8px;"></span>
          <router-link to="/signin" tag="a">注册</router-link>
        </div>
        <div class="header-right" v-if="hasLogin">
          <div class="menu menu1">
            <p class="template">资产</p>
            <div class="menu-choose1">
              <div style="display: flex;align-items: center;justify-content: space-around;width: 100%">
                <div v-for="item in menuList1" style="margin-top: 10px;">
                  <p>{{item.title}}</p>
                  <p>{{item.name}}</p>
                </div>
              </div>
              <p style="width: 90%;height: 1px;background-color: #d8d8d8;margin: 10px 0"></p>
              <div style="font-size:12px;color: #d8d8d8;display: flex;flex-direction: row;align-items: center;justify-content: space-between;width: 90%">
                <router-link to="/recharge" tag="a" style="color: #1c8de0;">充值提现</router-link>
                <router-link to="/bill" tag="a" style="color: #1c8de0;margin-left: 140px;">账单>></router-link>
                <p></p>
              </div>
            </div>
          </div>
          <div class="menu menu2">
            <p class="template">个人中心</p>
            <div class="menu-choose2">
              <p class="menu-choose2-item" v-for="(item,index) in titleList" @click="gotoPersonal(index)">{{item}}</p>
            </div>
          </div>
          <div class="menu menu3">
            <p class="template">消息</p>
            <div class="menu-choose3">
              <p v-for="(item,index) in newsList" style="font-size:12px;color: #333;width: 90%;text-align: left;">*{{item}}</p>
              <p style="width: 90%;height: 1px;background-color: #d8d8d8;margin: 8px 0"></p>
              <div style="font-size:12px;color: #d8d8d8;display: flex;flex-direction: row;align-items: center;justify-content: space-between;width: 90%">
                <router-link to="/home" tag="a" style="color: #1c8de0;">全部标为已读</router-link>
                <router-link to="/home" tag="a" style="color: #1c8de0;">查看全部消息>></router-link>
              </div>
            </div>
          </div>
        </div>
        <div class="header-right hover" style="margin-left: 10px;position: relative">
          <a style="font-size:16px;color: #fff;">{{language?'中文版':'English'}}</a>
          <a class="page" @click="CHOOSE_LANGUAGE">{{language?'English':'中文版'}}</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'
import { removeStore ,isNull} from '../../utils/index'

export default {
  data () {
    return {
      stationList:[{name:'BitFinex',price:'4,654.60USD'},{name:'Kraken',price:'3,654.00EUR'},{name:'Huobi',price:'38,654.40CNY'}],
      menuList1:[{title:'币种',name:'BTC'},{title:'总资产',name:'888'},{title:'冻结',name:'888'},{title:'可用',name:'888'}],
      newsList:['身份验证成功','身份验证提交审核','买家已经注销交易'],
      titleList:['个人资料','账户安全','支持工单','退出']
    }
  },
  computed: {
    ...mapState(['userInfo','hasLogin','language'])
  },
  mounted:function () {
    this.getUesrInfo();
  },
  methods: {
    ...mapMutations(['INIT_USER','EMPTY_USER','CHANGE_LOGIN','CHOOSE_LANGUAGE']),
    gotoPersonal(index){
      switch(index){
        case 0:this.$router.push('/personal');
        break;
        case 1:this.$router.push('/personal/safety');
        break;
        case 2:this.$router.push('/bill');
        break;
        case 3:this.$router.push('/');this.quit();
        break;
        default:console.log('超出的选项')
      }
    },
    quit:function () {
      console.log('退出');
      removeStore('userInfo');
      this.EMPTY_USER();
      this.getUesrInfo();
      this.CHANGE_LOGIN(false);
    },
    getUesrInfo(){
      this.INIT_USER();
      if(isNull(!this.userInfo)){
        console.log('有缓存'+!isNull(this.userInfo))
        this.CHANGE_LOGIN(true);
      }
      else {
        this.CHANGE_LOGIN(false);
      }
      console.log(this.userInfo)
    }
  }
}
</script>

<style scoped>
  a{color: #fff}
  .header{
    background-color: #2b2b2b;
    height:30px;
    line-height: 30px;
    min-width: 1200px;
  }
  .header-left{
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  }
  .header-left span{
    font-size: 14px;
    color: #fff;
    font-family: PingFangSC-Regular;
    letter-spacing: -0.24px;
    margin-right: 50px;
  }
  .header-right{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }
  .header-right span{
    font-size:12px;
    color: #fff;
    height: 30px;
    line-height: 30px;
  }
  .menu{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .menu1{
    width: 80px;
  }
  .menu2{
    width: 110px;
  }
  .menu3{
    width: 100px;
  }
  .template{
    font-size:12px;
    color: #d8d8d8;
  }
  .menu-choose1,.menu-choose2,.menu-choose3{
    display: none;
    z-index: 999;
  }
  .menu:hover{
    background-color: #fff;
  }
  .menu:hover .template{
    color: #333;
  }
  .menu1:hover .menu-choose1{
    width: 268px;
    height: 120px;
    position: absolute;
    left: -94px;
    top:30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.10);
    border-radius: 12px;
    overflow: hidden;
  }
  .menu2:hover .menu-choose2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width:110px;
    height: 160px;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.10);
    border-radius: 0 0 12px 12px;
    overflow: hidden;
    position: absolute;
    left: 0;
    top:30px;
  }
  .menu3:hover .menu-choose3{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 248px;
    background: #FFFFFF;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.10);
    border-radius: 12px;
    overflow: hidden;
    position: absolute;
    left: -74px;
    top:30px;
  }
  .menu-choose2-item{
    font-size:12px;color: #333;width: 110px;height: 40px;line-height: 40px;text-align: center;
  }
  .menu-choose2-item:hover{
    background-color: #1c8de0;
    color: #fff;
  }
  .page{
    font-size:16px;color: #333;position: absolute;left: 0;top: 30px;
    display: none;
  }
  .hover:hover .page{
    display: block;
  }
</style>



