         <img src="../../images/head.jpg" alt="" style="width: 90px;height: 90px;border-radius: 50%">
         <div style="margin-left: 30px">
           <p>{{user.nickname}}</p>
           <p style="display: flex;align-items: center;margin-top: 15px;">
             <img src="../../images/buy/online.png" alt="" style="width: 18px;height: 18px;" v-if="isOnline">
             <img src="../../images/buy/off-line.png" alt="" style="width: 18px;height: 18px;" v-if="!isOnline">
             <span style="margin-left: 5px;">{{isOnline?'在线':'离线'}}</span>
             <img src="../../images/buy/address.png" alt="" style="width: 14px;height: 18px;margin-left: 10px;">
             <span style="margin-left: 5px;">{{product.country_chinese_name}}</span>
           </p>
         </div>
         <div style="margin-left: 100px;display: flex;flex-direction: column;align-items: center">
           <p style="font-size:18px;color: #333">22</p>
           <p style="margin-top: 15px;font-size:14px;color: #666">信任人数</p>
         </div>
         <div style="margin-left: 100px;display: flex;flex-direction: column;align-items: center">
           <p style="font-size:18px;color: #333">100%</p>
           <p style="margin-top: 15px;font-size:14px;color: #666">好评</p>
         </div>
         <div style="margin-left: 100px;display: flex;flex-direction: column;align-items: center">
           <p style="font-size:18px;color: #333">131次</p>
           <p style="margin-top: 15px;font-size:14px;color: #666">交易次数</p>
         </div>
         <div style="margin-left: 100px;display: flex;flex-direction: column;align-items: center">
           <p style="font-size:18px;color: #333">22分钟</p>
           <p style="margin-top: 15px;font-size:14px;color: #666">平均放行</p>
         </div>
       </div>
       <div class="common">
         <div>
           <h3>价格</h3>
           <p><span style="font-size:28px;color:#DA0101;">{{product.price}}</span>CNY/BTC</p>
         </div>
         <div>
           <h3>付款方式</h3>
           <p>{{product.payment_chanel_names}}</p>
         </div>
         <div>
           <h3>交易金额</h3>
           <p>2000CNY</p>
         </div>
         <div>
           <h3>所在地</h3>
           <p>{{product.country_chinese_name}}</p>
         </div>
         <div>
           <h3>付款期限</h3>
           <p>
             <span>90分钟</span>
             <img src="../../images/buy/question.png" alt="" style="width: 16px;height: 16px;">
           </p>
         </div>
         <div>
           <img src="../../images/buy/report.png" alt="" style="width: 15px;height: 17px;">
           <p style="font-size:16px;color: #314E8C">举报这则交易广告</p>
         </div>
       </div>
       <!--未登录-->
       <div v-if="hasLogin">
         <p style="font-size:20px;margin-top: 17px;color: #E88305">注册并立即购买比特币</p>
         <div style="margin-top: 20px;;display: flex;align-items: center">
           <div style="width: 360px;height: 50px;display: flex;justify-content: center;align-items: center;background: #314E8C;border-radius: 4px;" @click="$router.push('/signin')">
             <img src="../../images/buy/register.png" alt="" style="width: 16px;height: 16px;">
             <p style="font-size:20px;color: #fff;margin-left: 5px;">30秒完成注册</p>
           </div>
           <p style="margin-left: 20px;">已有账号，<router-link to="/login" tag="a" style="color: #314E8C">立即登录</router-link></p>
         </div>
       </div>
       <!--已登录-->
       <div style="margin-top: 32px;">
         <h2 style="font-size:18px;color: #333;margin-bottom: 20px;">您想购买多少？</h2>
         <div style="display: flex;align-items: center">
           <div style="width: 368px;height: 54px;display: flex;align-items: center;border: 1px solid #DEDEDE">
             <input type="text"
                    placeholder="请输入您想购买的金额"
                    style="border: none;width:300px;height: 54px;line-height: 54px;padding-left: 10px;font-size:16px;color: #333">
             <p style="font-size:16px;color: #333;margin-left: 10px;">CNY</p>
           </div>
           <img src="../../images/buy/conversion.png" alt="" style="width: 20px;height: 13px;margin: 0 22px;">
           <div style="width: 368px;height: 54px;display: flex;align-items: center;border: 1px solid #DEDEDE">
             <input type="text"
                    placeholder="请输入您想购买的金额"
                    style="border: none;width:300px;height: 54px;line-height: 54px;padding-left: 10px;font-size:16px;color: #333">
             <p style="font-size:16px;color: #333;margin-left: 10px;">BTC</p>
           </div>
         </div>
         <h2 style="font-size:18px;color: #333;margin: 20px 0;">您的要求</h2>
         <p style="border-bottom:1px solid #DEDEDE;width: 800px;text-align: right;font-size:24px;color:#314E8C;margin-right: 4px;display: flex">
           <input type="text"
                  placeholder="告诉他您的要求(可不填)"
                  style="width:800px;text-align: left;font-size:16px;color: #333;border: none;height: 30px;line-height: 30px;"
                  maxlength="22">
         </p>
         <p style="width: 360px;height: 50px;line-height: 50px;text-align: center;background: #314E8C;border-radius: 4px;font-size:20px;color: #fff;margin-top: 30px;">买入比特币</p>
       </div>
       <div class="page-footer">
         <div style="display: flex;align-items: center;padding-top: 33px;">
           <img src="../../images/buy/clause.png" alt="" style="width: 30px;height: 30px;">
           <h2 style="font-size: 16px;color: #333333;margin-left: 10px;">htatt的交易条款</h2>
         </div>
         <p style="margin-top:30px">1.交易前請詳細了解對方的交易信息。</p>
         <p>2.請通過平台進行溝通約定，並保存好相關聊天記錄。</p>
         <p>3.如遇到交易糾紛，可通過申訴來解決問題。</p>
         <p>4.在您發起交易請求後，比特幣被鎖定在託管中，受到CoinCola保護。如果您是買家，發起交易請求後，請在付款週期內付款並把交易標記為付款已完成。賣家在收到付款後將會放行處於託管中的比特幣。
           交易前請閱讀《CoinCola服務條款》以及常見問題，交易指南等幫助文檔。</p>
         <p>5.請注意欺詐風險，交易前請檢查該用戶收到的評價和相關信用信息，並對新近創建的賬戶多加留意。</p>
         <p>6.託管服務保護網上交易的買賣雙方。在發生爭議的情況下，我們將評估所提供的所有信息，並將託管的比特幣放行給其合法所有者</p>
       </div>
     </div>
   </div>
 </div>
</template>

<script>
import { mapState } from 'vuex'
import {httpGet} from '../../utils/index'

export default {
  data () {
    return {
      id:'',
      product:{},
      user:{},
      isOnline:false,
      input:''
    }
  },
  computed: {
    ...mapState(['hasLogin'])
  },
  mounted(){
    this.id = this.$route.params.id;
    this.getProductsDetail(this.id);
  },
  methods: {
    getProductsDetail(id){
      var that = this;
      httpGet('/products/detail',{product_id:id},function (res) {
        if(res.data.error_code == 0){
          that.product = res.data.product;
          that.user = res.data.product.user;
          var timestamp = Date.parse(new Date());
          if(that.user.last_active_at == timestamp){
            that.isOnline = true;
          }
        }
        else {
          console.log(res.data.error_reason)
        }
      })
    }
  }
}
</script>

<style scoped>
  input:input-placeholder{
    color: red;
  }
  .containers{
    width: 1200px;
    margin: 0 auto;
  }
  .banner{
    background: url("../../images/buy/banner.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 220px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 50px;
  }
  .banner h2{
    font-family: PingFangSC-Regular;
    font-size: 34px;
    color: #FFFFFF;
  }
  .banner p{
    ont-family: PingFangSC-Regular;
    font-size: 20px;
    color: #FFFFFF;
    margin-top: 35px;
  }
  .details{
    background-color: #fff;
    margin-top: 9px;
    padding: 0 50px;
  }
  .header{
    height: 200px;
    border-bottom: 2px solid #EFF3F5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .common>div{
    margin-top: 23px;
    display: flex;
    align-items: center;
  }
  .common>div h3{
    font-size: 16px;
    color: #666666;
    width: 340px;
  }
  .common>div p{
    font-size: 16px;
    color: #666666;
  }
  .page-footer{
    border-top: 1px dotted #D6D6D6;
    margin-top:38px;
    padding-bottom: 50px;
  }
  .page-footer p{
    font-size: 12px;
    color: #666666;
    line-height: 22px;
  }
</style>



