// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import headers from './components/header/index.vue'
import footers from './components/footer/index.vue'
import navs from './components/nav/2.vue'
import tabs from './components/tab/index.vue'
import tables from './components/table/index.vue'

Vue.use(ElementUI)
Vue.config.productionTip = false

// 注册组件
Vue.component('header-Top', headers)
Vue.component('footer-Bottom', footers)
Vue.component('nav-Top', navs)
Vue.component('tab-Top', tabs)
Vue.component('table-Top', tables)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App }
})



