<template>
  <div class="content">
    <img src="../../../../images/reset/email.png" alt="">
    <h2>已发送邮件到您邮件，请登录邮箱重置密码</h2>
    <p>邮件已发送到邮箱 zhoubo@365yf.com</p>
    <div @click="next()">
      <span>重新发送邮件</span>
    </div>
  </div>
</template>
<script>
  import { mapState } from 'vuex'

  export default {
    data () {
      return {

      }
    },
    computed: {
      ...mapState({
        // isCollapse: state => state.isCollapse
      })
    },
    methods: {
      next(){
        this.$router.push('/reset/revise')
      }
    }
  }
</script>
<style scoped>
  .content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
  .content img{
    width: 108px;
    height: 76px;
    margin-top: 87px;
  }
  .content h2{
    font-family: PingFangSC-Regular;
    font-size: 24px;
    color: #333333;
    letter-spacing: -0.41px;
    margin-top: 50px;
  }
  .content p{
    font-family: PingFangSC-Regular;
    font-size: 18px;
    color: #666666;
    letter-spacing: -0.31px;
    margin-top: 20px;
  }
  .content div{
    width: 260px;
    height: 50px;
    border: 1px solid #314E8C;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
  }
  .content div span{
    font-family: PingFangSC-Regular;
    font-size: 20px;
    color: #314E8C;
    letter-spacing: -0.34px;
  }

</style>



