<template>
 <div>
   <div class="banner">
     <div class="banner-inner" v-if="!hasLogin">
       <form action="user" class="my-input" style="margin-top: 70px">
         <img src="../../images/home/user.png" alt="">
         <input type="text" name="user" placeholder="用户名" v-model="username">
       </form>
       <form action="password" class="my-input" style="margin-top: 34px">
         <img src="../../images/home/password.png" alt="">
         <input type="text" name="password" placeholder="密码" v-model="password">
       </form>
       <div class="submit" style="margin-top: 44px" @click="logIn">
         <p style="font-size:20px;color: #fff">登录</p>
       </div>
       <div style="width: 300px;text-align: justify;margin-top: 20px;display: flex;justify-content: space-between">
         <router-link to="/signin" tag="a" style="color: #fff;font-size:12px;">注册</router-link>
         <router-link to="/reset" tag="a" style="color: #fff;font-size:12px;">忘记密码？</router-link>
       </div>
     </div>
   </div>
   <div class="content">
     <div class="container">
       <!--出售比特币列表-->
       <div class="list">
         <div class="list-title" style="margin-top: 57px;">
           <p>购买比特币</p>
         </div>
         <div class="headline">
           <el-cascader
             :options="options2"
             :show-all-levels="false"
           ></el-cascader>
           <el-select v-model="value" placeholder="交易类型" class="el-select">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
           <el-select v-model="value" placeholder="支付方式" class="el-select">
           <el-option
             v-for="item in options"
             :key="item.value"
             :label="item.label"
             :value="item.value">
           </el-option>
           </el-select>
           <el-select v-model="value" placeholder="所有货币" class="el-select">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
           <el-button type="primary" style="background-color: #314E8C;width: 100px;"><i class="el-icon-search" style="margin-right: 10px"></i>搜索</el-button>
         </div>
         <div v-for="(item,index) in products" :class="['tran-item','bottom-line']">
           <div class="products-face" @click="lookOthers(item.id)">
             <img src="http://otc_dev.365yf.cc/images/avatar/avatar.png" alt="">
             <p>在线</p>
           </div>
           <div class="products-name">
             <p>{{item.user.nickname}}</p>
             <p style="margin-top: 10px">{{item.country_chinese_name}}</p>
           </div>
           <div class="products-num">
             <p>交易量50</p>
             <p style="margin-top: 10px">好评100%</p>
           </div>
           <div class="products-pay">{{item.payment_chanel_names}}</div>
           <div class="products-price">
             <p>{{item.price}}(CNY)</p>
             <p style="margin-top: 10px">限额{{item.min_trade_amount}}-{{item.max_trade_amount}}</p>
           </div>
           <div class="products-buy" @click="buyBtc(item.id)">
             <p>买入</p>
           </div>
         </div>
         <div style="width: 84%;margin:0 auto;display: flex;justify-content: flex-end;align-items: ce;margin-top: 20px;">
           <router-link to="/home" tag="a" style="color: #314E8C;;font-size:12px;">查看更多>></router-link>
         </div>
       </div>
       <!--出售比特币列表-->
       <div class="list">
         <div class="list-title" style="margin-top: 23px;">
           <p>出售比特币</p>
         </div>
         <div class="headline">
           <el-select v-model="value" placeholder="国家" class="el-select">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
           <el-select v-model="value" placeholder="交易类型" class="el-select">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
           <el-select v-model="value" placeholder="支付方式" class="el-select">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
           <el-select v-model="value" placeholder="所有货币" class="el-select">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
           <el-button type="primary" style="background-color: #314E8C;width: 100px;"><i class="el-icon-search" style="margin-right: 10px"></i>搜索</el-button>
         </div>
         <div v-for="(item,index) in products" :class="['tran-item','bottom-line']">
           <div class="products-face" @click="lookOthers">
             <img src="http://otc_dev.365yf.cc/images/avatar/avatar.png" alt="">
             <p>在线</p>
           </div>
           <div class="products-name">
             <p>{{item.user.nickname}}</p>
             <p style="margin-top: 10px">{{item.country_chinese_name}}</p>
           </div>
           <div class="products-num">
             <p>交易量50</p>
             <p style="margin-top: 10px">好评100%</p>
           </div>
           <div class="products-pay">{{item.payment_chanel_names}}</div>
           <div class="products-price">
             <p>{{item.price}}(CNY)</p>
             <p style="margin-top: 10px">限额{{item.min_trade_amount}}-{{item.max_trade_amount}}</p>
           </div>
           <div class="products-buy" style="background-color: #de0000" @click="sellBtc(item.id)">
             <p>卖出</p>
           </div>
         </div>
         <div style="width: 84%;margin:0 auto;display: flex;justify-content: flex-end;margin-top: 20px;margin-bottom: 42px;">
           <router-link to="/home" tag="a" style="color: #314E8C;;font-size:12px;">查看更多>></router-link>
         </div>
       </div>
     </div>
   </div>
 </div>
</template>

<script>
import { mapState ,mapMutations} from 'vuex'
import { httpGet,setStore } from '../../utils/index'

export default {
  data () {
    return {
      Asia: [],
      Europe: [],
      Africa: [],
      North: [],
      South: [],
      Oceania: [],
      Antarctica: [],
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      options2: [
        {
        value: 'yazhou',
        label: '亚洲',
        children: [{

          value: 'id',
          label: '中国'
        }, {

          value: '2',
          label: '日本'
        }, {

          value: '3',
          label: '韩国'
        }]
      }, {
          value: 'ouzhou',
          label: '欧洲',
          children: [{
            value: '4',
            label: '英国'
          }, {
            value: '5',
            label: '雅典'
          }, {
            value: '6',
            label: '奥尔兰'
          }]
        }],
      value: '',
      products: [],
      username: '',
      password: ''
    }
  },
  computed: {
    ...mapState(['hasLogin', 'countries','language'])
  },
  mounted: function () {
    this.getProducts();
    this.getCountries();
  },
  methods: {
    ...mapMutations(['INIT_USER', 'CHANGE_LOGIN', 'GET_COUNTRISE']),
    buyBtc (id) {
      this.$router.push('/buy/' + id)
    },
    sellBtc (id) {
      this.$router.push('/sell/' + id)
    },
    lookOthers (id) {
      this.$router.push('/others/' + id)
    },
    logIn () {
      var that = this;
      httpGet('/users/login', {email: that.username, password: that.password}, function (res) {
        if (res.data.error_code === 0) {
          console.log('sy登录成功');
          // 存入缓存
          setStore('userInfo', {email: that.username, password: that.password});
          that.INIT_USER();
          that.CHANGE_LOGIN(true)
        }
      })
    },
    getProducts(){
      var that = this;
      httpGet('/products',{},function (res) {
        that.products = res.data.products;
        console.log('首页console')
      })
    },
    getCountries(){
      var that = this;
      httpGet('/countries',{pid: 1, level: 2},function (res) {
        if(res.data.error_code === 0){
          that.Asia = res.data.countries;
          that.Asia.forEach(function (item,index) {
            item.value = item.id;
            item.label = that.language?item.chinese_name:item.english_name;
          });
          that.GET_COUNTRISE({index: 0,children:that.Asia});
        }
      });
      httpGet('/countries',{pid: 2, level: 2},function (res) {
        if(res.data.error_code === 0){
          that.Europe = res.data.countries;
          that.Europe.forEach(function (item,index) {
            item.value = item.id;
            item.label = that.language?item.chinese_name:item.english_name;
          });
          that.GET_COUNTRISE({index: 1,children:that.Europe});
        }
      });
      httpGet('/countries',{pid: 3, level: 2},function (res) {
        if(res.data.error_code === 0){
          that.Africa = res.data.countries;
          that.Africa.forEach(function (item,index) {
            item.value = item.id;
            item.label = that.language?item.chinese_name:item.english_name;
          });
          that.GET_COUNTRISE({index: 2,children:that.Africa});
        }
      });
      httpGet('/countries',{pid: 4, level: 2},function (res) {
        if(res.data.error_code === 0){
          that.North = res.data.countries;
          that.North.forEach(function (item,index) {
            item.value = item.id;
            item.label = that.language?item.chinese_name:item.english_name;
          });
          that.GET_COUNTRISE({index: 3,children:that.North});
        }
      });
      httpGet('/countries',{pid: 5, level: 2},function (res) {
        if(res.data.error_code === 0){
          that.South = res.data.countries;
          that.South.forEach(function (item,index) {
            item.value = item.id;
            item.label = that.language?item.chinese_name:item.english_name;
          });
          that.GET_COUNTRISE({index: 4,children:that.South});
        }
      });
      httpGet('/countries',{pid: 6, level: 2},function (res) {
        if(res.data.error_code === 0){
          that.Oceania = res.data.countries;
          that.Oceania.forEach(function (item,index) {
            item.value = item.id;
            item.label = that.language?item.chinese_name:item.english_name;
          });
          that.GET_COUNTRISE({index: 5,children:that.Oceania});
        }
      });
      httpGet('/countries',{pid: 7, level: 2},function (res) {
        if(res.data.error_code === 0){
          that.Antarctica = res.data.countries;
          that.Antarctica.forEach(function (item,index) {
            item.value = item.id;
            item.label = that.language?item.chinese_name:item.english_name;
          });
          that.GET_COUNTRISE({index: 6,children:that.Antarctica});
        }
      });
    }
  }
}
</script>

<style scoped>
  .banner{
    width: 100%;
    height:600px;
    background-image: url("../../images/home/banner.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    z-index: 0;
  }
  .banner-inner{
    width: 360px;
    height:360px;
    background-color:rgba(255,255,255,0.5);
    margin-right: 15%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
  .my-input{
    width: 300px;
    height: 50px;
    background-color: #fff;
    border: 1px solid #979797;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  .my-input img{
    width: 20px;
    height: 24px;
    margin: 0 15px;
  }
  .my-input input{
    border: none;
    outline: none;
    text-decoration: none;
    height: 50px;
    width: 260px;
    line-height:50px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #999999;
    letter-spacing: -0.24px;
  }
  .submit{
    width: 300px;height: 50px;background: #314E8C;display: flex;justify-content: center;align-items: center
  }
  .container{
    width: 1200px;
    margin: 0 auto;
  }
  .list{
    width: 100%;
  }
  .list-title{
    width: 100%;
    height: 50px;
    background-color: #314E8C;
    display: flex;justify-content: center;align-items: center;
  }
  .list-title p{
    font-size:18px;
    color: #fff;
  }
  .headline{
    display: flex;
    justify-content: flexs-start;
    align-items: center;
    width: 860px;
    margin: 0 auto;
    border: 1px solid #314E8C;
    border-shadow:10px;
    overflow: hidden;
    margin-top: 50px;
    margin-bottom: 50px;
    height: 30px;
  }
  .tran-item{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 90%;
    height: 120px;
    margin: 0 5%;
    /*border: 1px solid #000;*/
  }
  .bottom-line{
    border-bottom: 1px solid #d8d8d8;
  }
  .products-face{
    width: 117px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
    /*border: 1px solid #000;*/
  }
  .products-face img{
    width: 41px;
    height: 41px;
    border-radius: 50%;
    margin-bottom: 5px;
  }
  .products-face p{
    font-size:14px;
    color: #333;
  }
  .products-name{
    /*border:1px solid #000;*/
    width: 186px;
    text-align: center;
  }
  .products-num{
    /*border:1px solid #000;*/
    width: 186px;
    text-align: center;
  }
  .products-pay{
    /*border:1px solid #000;*/
    width: 186px;
    text-align: center;
  }
  .products-price{
    /*border:1px solid #000;*/
    width: 186px;
    text-align: center;
  }
  .products-buy{
    width: 88px;
    height: 48px;
    background: #43C300;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.10);
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 90px;
  }
  .products-buy p{
    font-family: PingFangSC-Regular;
    font-size: 18px;
    color: #FFFFFF;
    letter-spacing: -0.31px;
  }
</style>



