<template>
 <div class="root">

   <div class="login">
     <div class="modal"></div>
     <div class="login_content">
       <p class="title">登录</p>
       <!--<hr class="l_line"/>-->
       <div class="l_line"></div>
       <div class="username">
         <input type="text" placeholder="用户名" v-model="username">
       </div>
       <div class="code">
         <input type="text" placeholder="密码" v-model="password">
       </div>
       <div class="l_btn" @click="logIn()">登录</div>
     </div>
   </div>
 </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import {httpGet,setStore} from '../../utils/index'

export default {
  data () {
    return {
      username:'',
      password:''
    }
  },
  computed: {
  },
  methods: {
    ...mapMutations([
      'CHANGE_LOGIN',
      'INIT_USER'
    ]),
    logIn(){
      var that = this;
      httpGet('/users/login',{email:that.username,password:that.password},function (res) {
        if(res.data.error_code === 0){
          console.log('登录成功');
          // 暂时跳首页 需求是前一页
          that.$router.push('/')
          // 存入缓存
          setStore('userInfo',{email:that.username,password:that.password});
          that.INIT_USER();
          that.CHANGE_LOGIN(true)
        }
        else {
          console.log(res.data.error_reason)
        }
      })
    },
  }
}
</script>

<style scoped>
  *{margin: 0;padding: 0}
  .root{
    padding: 207px 0 313px;
    background-image: url("../../images/login/bg.png");
  }
  .login{
    width: 400px;
    height: 350px;
    margin: 0 auto;
    position: relative;
  }
  .login .modal{
    width: 100%;
    height: 100%;
    position: absolute;
    /*background-color: rgb(130,160,194);*/
    background-color: #fff;
    opacity: 0.4;
  }
  .login_content{
    position: relative;
    left: 0;
    top:0;
  }
  .login p.title{
    width: 100%;
    height: 67px;
    text-align: center;
    line-height: 67px;
    font-family: PingFangSC-Regular;
    font-size: 24px;
    color: #FFF;
  }
  .login_content .l_line{
    height: 2px;
    background-color: #3A4E7B;
    /*color: #3A4E7B;*/
  }
  .login_content .username,.login_content .code{
    width: 333px;
    height: 50px;
    border-radius: 5px;
    margin: 0 auto;
    background-color: #fff;
  }
  .login_content .username{
    margin: 41px auto 20px;

  }
  .login_content .code{
    margin-bottom: 31px;
  }
  .login_content .username input,
  .login_content .code input{
    border: none;
    background-color:#fff;
    height: 50px;
    line-height: 50px;
    width: 100%;
    text-indent: 64px;
    outline: none;
  }
  .login_content .username input{
    background: url("../../images/login/user.png") no-repeat 20px center;
  }
  .login_content .code input{
    background: url("../../images/login/code.png") no-repeat 20px center;
  }
  .login_content .l_btn{
    width: 333px;
    height: 40px;
    border-radius: 5px;
    margin: 0 auto;
    background-color: #314E8C;
    text-align: center;
    line-height: 40px;
    font-family: PingFangSC-Regular;
    font-size: 18px;
    color: #FFFFFF;
    cursor: pointer;
  }
</style>



