import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
Vue.use(Vuex)
const state = {
  hasLogin: false,
  userInfo: '',
  language: true,
  countries: [
    {
      value: '1',
      label: this.language ? '亚洲':'Asia',
      children: ''
    },
    {
      value: '2',
      label: this.language ? '欧洲':'Europe',
      children: ''
    },
    {
      value: '3',
      label: this.language ? '非洲':'Africa',
      children: ''
    },
    {
      value: '4',
      label: this.language ? '北美':'North America',
      children: ''
    },
    {
      value: '5',
      label: this.language ? '南美':'South America',
      children: ''
    },
    {
      value: '6',
      label: this.language ? '大洋洲':'Oceania',
      children: ''
    },
    {
      value: '7',
      label: this.language ? '南极洲':'Antarctica',
      children: ''
    },
   ],
}
export default new Vuex.Store({
  state,
  mutations,
  actions
})



