import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home/index'
import index from '@/pages/index/index'
import bill from '@/pages/bill/index'
import login from '@/pages/login/index'
import signin from '@/pages/signin/index'
import release from '@/pages/release/index'
import wallet from '@/pages/wallet/index'
import balance from '@/pages/wallet/children/balance/index'
import recharge from '@/pages/wallet/children/recharge/index'
import withdraw from '@/pages/wallet/children/withdraw/index'
import help from '@/pages/help/index'
import sell from '@/pages/sell/index'
import buy from '@/pages/buy/index'
import others from '@/pages/others/index'
import personal from '@/pages/personal/index'
import identity from '@/pages/personal/children/identity/index'
import safety from '@/pages/personal/children/safety/index'
import advertising from '@/pages/personal/children/advertising/index'
import trust from '@/pages/personal/children/trust/index'
import trust1 from '@/pages/personal/children/trust/children/tab1/index'
import trust2 from '@/pages/personal/children/trust/children/tab2/index'
import trust3 from '@/pages/personal/children/trust/children/tab3/index'
import basic from '@/pages/personal/children/basic/index'
import deal from '@/pages/personal/children/deal/index'
import deal1 from '@/pages/personal/children/deal/children/tab1/index'
import deal2 from '@/pages/personal/children/deal/children/tab2/index'
import deal3 from '@/pages/personal/children/deal/children/tab3/index'
import deal4 from '@/pages/personal/children/deal/children/tab4/index'
import reset from '@/pages/reset/index'
import email from '@/pages/reset/children/email/index'
import success from '@/pages/reset/children/success/index'
import revise from '@/pages/reset/children/revise/index'
import miss from '@/pages/404/index'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: index,
      children: [
        { path: '', component: home },
        { path: 'bill', component: bill },
        { path: 'others', component: others },
        { path: 'login', component: login },
        { path: 'others/:id', component: others },
        { path: 'login', component: login },
        { path: 'signin', component: signin },
        { path: 'release', component: release },
        { path: 'help', component: help },
        { path: 'buy/:id', component: buy },
        { path: 'sell/:id', component: sell },
        {
          path: '/personal',
          component: personal,
          children: [
            { path: '', redirect: 'basic' },
            { path: 'basic', component: basic },
            { path: 'identity', component: identity },
            { path: 'safety', component: safety },
            { path: 'advertising', component: advertising },
            { path: 'trust',
              component: trust,
              children:[
                { path: '', redirect: 'tab1' },
                { path: 'tab1', component: trust1 },
                { path: 'tab2', component: trust2 },
                { path: 'tab3', component: trust3 }
              ]
            },
            { path: 'deal',
              component: deal,
              children:[
                { path: '', redirect: 'tab1' },
                { path: 'tab1', component: deal1 },
                { path: 'tab2', component: deal2 },
                { path: 'tab3', component: deal3 },
                { path: 'tab4', component: deal4 }
              ]
            }
          ]
        },
        {
          path: '/wallet',
          component: wallet,
          children:[
            { path: '', redirect: 'balance' },
            { path: 'balance', component: balance },
            { path: 'recharge', component: recharge },
            { path: 'withdraw', component: withdraw }
          ]
        },
        {
          path: 'reset',
          component: reset,
          children: [
            { path: '', redirect: 'email' },
            { path: 'email', component: email },
            { path: 'success', component: success },
            { path: 'revise', component: revise }
          ]
        }
      ]
    },
    { path: '*', component: miss }
  ]
})



