<template>
  <div class="content">
    <div class="tab">
      <div :class="['item',tabIndex==index?'item-current':'']" v-for="(item,index) in tabList" @click="tabClick(index)">
        <img src="../../images/a0.png" alt="" v-if="index == 0 && tabIndex != 0">
        <img src="../../images/a1.png" alt="" v-if="index == 0 && tabIndex == 0">
        <img src="../../images/b0.png" alt="" v-if="index == 1 && tabIndex != 1">
        <img src="../../images/b1.png" alt="" v-if="index == 1 && tabIndex == 1">
        <img src="../../images/c0.png" alt="" v-if="index == 2 && tabIndex != 2">
        <img src="../../images/c1.png" alt="" v-if="index == 2 && tabIndex == 2">
        <img src="../../images/c0.png" alt="" v-if="index == 3 && tabIndex != 3">
        <img src="../../images/c1.png" alt="" v-if="index == 3 && tabIndex == 3">
        <p :class="[tabIndex == index?'pcurrent':'']">{{item.name}}</p>
      </div>
    </div>
    <p class="line"></p>
    <router-view></router-view>
  </div>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    data () {
      return {
        tabList:[{name:'进行中的交易'},{name:'已完成的交易'},{name:'已取消的交易'},{name:'申诉中的交易'}],
        tabIndex:0,
        navIndex:0
      }
    },
    computed: {
      ...mapState({
        // isCollapse: state => state.isCollapse
      })
    },
    methods: {
      tabClick(tab) {
        this.tabIndex = tab;
        this.navIndex = tab + 1;
        this.$router.push('/personal/deal/tab' + this.navIndex);
      }
    }
  }
</script>

<style scoped>
  .content{
    padding: 0 40px;
  }
  .tab{
    margin-top: 20px;
    display: flex;
  }
  .item{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 180px;
    padding-bottom: 20px;
    margin-right: 30px;
  }
  .item-current{
    border-bottom: 2px solid #314E8C;
  }
  .item img{
    width: 22px;
    height: 20px;
    margin-right: 10px;
  }
  .item p{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333;
  }
  .item p.pcurrent{
    color: #314E8C;
  }
  .line{
    height: 2px;
    background-color: #d6d6d6;
  }
</style>



